import React, { Component } from "react";
import { Link } from "react-router-dom";
import { TweenLite } from "gsap";
import ScrollToPlugin from "gsap/ScrollToPlugin";

import maeImage from "../images/mae-2.png";
import helpIcon from "../images/question-circle.svg";

function GetRandomMessage() {
  const r = Math.random();
  if (r < 0.33) {
    return { text: "Nice to meet you, I'm Paola.", speaker: "Paola" };
  } else if (r < 0.66) {
    return { text: "How do you know Ashley?", speaker: "Player" };
  } else {
    return { text: "She's my cousin! We go way back.", speaker: "Paola" };
  }
}

function TestMessageChunk() {
  const r = Math.random();
  if (r < 0.33) {
    return (
      <div className="conversation__message conversation__message--right">
        <div className="conversation__bubble">Nice to meet you, I'm Paola.</div>
      </div>
    );
  } else if (r < 0.66) {
    return (
      <div className="conversation__message conversation__message--left">
        <div className="conversation__bubble">How do you know Ashley?</div>
      </div>
    );
  } else {
    return (
      <div className="conversation__message conversation__message--right">
        <div className="conversation__bubble">She's my cousin! We go way back.</div>
      </div>
    );
  }

  // return (
  //   <React.Fragment>
  //     <div className="conversation__message conversation__message--right">
  //       <div className="conversation__bubble">Nice to meet you, I'm Paola.</div>
  //     </div>
  //     <div className="conversation__message conversation__message--left">
  //       <div className="conversation__bubble">How do you know Ashley?</div>
  //     </div>
  //     <div className="conversation__message conversation__message--right">
  //       <div className="conversation__bubble">She's my cousin! We go way back.</div>
  //     </div>
  //   </React.Fragment>
  // );
}

export default class Paola extends Component {
  constructor(props) {
    super(props);

    this.state = {
      messages: [],
      numMessages: 4
    };
    this.scrollTween = null;
  }

  componentDidMount() {
    this.scrollTween = TweenLite.to(this.messagesContainer, 1, {
      scrollTo: this.messagesContainer.scrollHeight
    });

    const newMessage = () => {
      this.setState(prevState => ({ numMessages: (prevState.numMessages += 1) }));
      this.setState(prevState => ({ messages: [...prevState.messages, GetRandomMessage()] }));
    };

    setInterval(newMessage, 1000);
    newMessage();
  }

  componentDidUpdate() {
    if (this.scrollTween) this.scrollTween.kill();
    this.scrollTween = TweenLite.to(this.messagesContainer, 1, {
      scrollTo: this.messagesContainer.scrollHeight
    });
  }

  componentWillUnmount() {
    if (this.scrollTween) this.scrollTween.kill();
  }

  render() {
    const messages = [];
    for (let i = 0; i < this.state.numMessages; i++) {
      messages.push(<TestMessageChunk />);
    }

    return (
      <div>
        <div className="fixed-navbar">
          <img className="fixed-navbar__portrait" src={maeImage} alt="Mae Portrait" />
          <div className="fixed-navbar__center">Interviewing Paola</div>
          <img className="fixed-navbar__help" src={helpIcon} alt="Help" />
        </div>

        {/* <div className="conversation" ref={ref => (this.messagesContainer = ref)}>
          {messages}
        </div> */}

        <div className="conversation" ref={ref => (this.messagesContainer = ref)}>
          {this.state.messages.map(msg => (
            <div
              className={`conversation__message conversation__message--${
                msg.speaker === "Player" ? "left" : "right"
              }`}
            >
              <div className="conversation__bubble">{msg.text}</div>
            </div>
          ))}
        </div>

        <div className="options-menu">
          <img className="options-menu__portrait" src={maeImage} alt="Mae Portrait" />
          <ul className="options-menu__list">
            <li className="options-menu__list-item options-menu__list-item--visited">
              Ask how they know your boss.
            </li>
            <li className="options-menu__list-item">Ask about existing debt.</li>
            <li className="options-menu__list-item">Ask about why they want a loan.</li>
            <li className="options-menu__list-item options-menu__list-item--visited">
              Ask about living in Chicago.
            </li>
            <li className="options-menu__list-item">Ask about what they do.</li>
            <li className="options-menu__list-item">Talk to someone else.</li>
          </ul>
        </div>
      </div>
    );
  }
}



