import React, { Component } from "react";
import Typist from "react-typist";

import PaolaPage from "./pages/paola";
import Interview from "./pages/interview";

import ConversationTree from "./conversation-tree/conversation-tree";
import paolaDialogueData from "./conversation-tree/paola-dialogue.json";
const paolaTree = new ConversationTree(paolaDialogueData);

class App extends Component {
  render() {
    return <Interview conversationTree={paolaTree} />;
  }
}
export default App;



